<template>
  <div class="chat-screen d-flex flex-column ">
    <div class="message-container p-4">
      <div
        :key="message.id"
        v-for="message in messages"
        :class="{
          incoming: !isCustomMessage(message.user),
          'outgoing justify-content-end': isCustomMessage(message.user),
        }"
        class="d-flex"
      >
        <span class="message d-flex flex-column mt-1 px-3 py-2">
          {{ message.message }}
          <small class="date d-flex justify-content-end">{{
            message.time
          }}</small>
        </span>
      </div>

      <!-- <div class="outgoing d-flex justify-content-end">
        <span class="message d-flex flex-column mt-1 px-3 py-2">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Ut, nulla?
          <small class="date d-flex justify-content-end">17:40</small>
        </span>
      </div> -->
    </div>

    <div class="input bg-dark d-flex align-items-center px-3">
      <VEmojiPicker
        class="emoji-picker"
        :class="{ show: pickerIsShow }"
        @select="selectEmoji"
      />
      <i
        @click="pickerIsShow = !pickerIsShow"
        style="font-size:40px"
        class="far fa-laugh text-secondary"
      ></i>
      <div class="input-field col s12 d-flex align-items-center">
        <input
          v-model="text"
          @keyup.enter="sendMessage"
          type="text"
          class="validate text-white"
        />
      </div>
      <i
        @click="sendMessage"
        style="font-size:40px"
        class="fas fa-paper-plane text-secondary pl-2"
      ></i>
    </div>
  </div>
</template>

<script>
import VEmojiPicker from "v-emoji-picker";
import io from "socket.io-client";

export default {
  data() {
    return {
      pickerIsShow: false,
      text: "",
      socketMessage: "",
      socket: io("http://localhost:5000"),
      messages: [],
    };
  },
  components: {
    VEmojiPicker,
  },
  methods: {
    isCustomMessage(username) {
      return this.$route.query.username === username;
    },
    selectEmoji(emoji) {
      this.text += emoji.data;
    },
    sendMessage() {
      if (this.text) {
        let username = this.$route.query.username;
        const time = new Date().getHours() + ":" + new Date().getMinutes();

        this.socket.emit("chatMessage", {
          user: username,
          message: this.text,
          time,
          id:Math.random()
        });
        this.clearInput();
      }
    },
    clearInput() {
      this.text = null;
    },
  },
  created() {
    this.socket.on("incomingMessage", (message) => {
      this.messages.push(message)
      console.log(message)
    });

    // window.addEventListener("click", (e) => {
    //   let elementsClasses = e.target.classList;
    //   if (
    //     elementsClasses.contains("chat-screen") ||
    //     elementsClasses.contains("materialize-textarea")
    //   ) {
    //     this.pickerIsShow = false;
    //   }
    // });
  },
};
</script>

<style scoped>
.chat-screen {
  height: 80vh;
  background: url("../../assets/chat-bg.png");
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  position: relative;
  width: 100%;
}

.incoming .message {
  color: white;
  background: #262d31;
  border-radius: 15px;
  max-width: 80%;
}

.outgoing .message {
  color: white;
  background: #054740;
  border-radius: 15px;
  max-width: 80%;
}
.input {
  position: absolute;
  bottom: 0;
  width: 100%;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}
.emoji-picker {
  position: absolute;
  left: 10px;
  top: -550%;
  display: none !important;
}
.show {
  display: block !important;
}
i {
  cursor: pointer;
}
i:hover {
  color: #a2bebb !important;
}
</style>
